<style lang="scss">
.container{
  table{ width: 100%;}
  td{ float:none;}
}

</style>
<template>
<div class="fr-w container">
  <h3 style="line-height:2em;margin-top:1em;">
  <el-button
      type="primary"
      @click.native="openFullScreen"
      v-loading.fullscreen.lock="fullscreenLoading">
      3 
    </el-button>
  </h3>

  <h3 style="line-height:2em;margin-top:1em;">  </h3>

  <div class="bt-input"><input type="text" placeholder="input"></div>
  <br>
  <div class="bt-input"><textarea  placeholder="textarea"></textarea></div>

  <h3 style="line-height:2em;margin-top:1em;"> v-model  </h3>

  <table>
    <thead>
      <tr>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </tr>
    </thead>
    <tbody>
      <tr class="row">
        <td class="col-xl-3">
          <el-checkbox v-model="bcheck"></el-checkbox>
        </td>
        <td class="col-xl-3">
          <el-select v-model="mselects" filterable clearable placeholder="">
            <el-option v-for="item in options" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </td>
        <td class="col-xl-3">
          <el-checkbox v-model="bcheck"></el-checkbox>
        </td>
        <td class="col-xl-3">
          <el-checkbox v-model="bcheck"></el-checkbox>
        </td>
      </tr>
    </tbody>
  </table>
  <table>
      <tr>
        <td><el-input placeholder="A" v-model="inputa"></el-input></td>
        <td><el-input placeholder="B" v-model="inputb"></el-input></td>
        <td><address-box v-model="address" data-url="./static/data/address-data.json"></address-box></td>
      </tr>
    </table>

  <h3 style="line-height:2em;margin-top:1em;">Customer Table  v-for  v-model  vuex </h3>

  <custom-table :data="tableData">
    <custom-row v-for="(item, index) in tableData" :index="index">
      <custom-column label="" prop="date" twidth="80">
        <el-input :value="item.date"/>
      </custom-column>
      <custom-column label="" prop="name" twidth="80">
        <el-input :value="item.name"/>
      </custom-column>
      <custom-column label="" prop="choose" twidth="80">
        <el-select :value="item.choose" placeholder="">
          <el-option v-for="opt in options" :label="opt.label" :value="opt.value"></el-option>
        </el-select>
      </custom-column>
      <custom-column label="" prop="schecked" twidth="80">
        <el-checkbox :value="item.schecked" ></el-checkbox>
      </custom-column>
    </custom-row>
  </custom-table>

  <h3 style="line-height:2em;margin-top:1em;">EL-Table </h3>

  <el-table :data="tableData" borderstyle="width: 100%" @selection-change="handleSelectionChange">
    <el-table-column type="selection" width="50" label=""></el-table-column>
    <el-table-column inline-template label="" width="120">
      <div>{{ row.date }}</div>
    </el-table-column>
    <el-table-column type="input" prop="name" label="" width="120"></el-table-column>
    <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
    <el-table-column type="select"  :option-list="options" prop="choose" label=""></el-table-column>
    <el-table-column type="select"  :option-list="options" prop="choose" label=""></el-table-column>
    <el-table-column type="checkbox" prop="schecked" label=""></el-table-column>
    <el-table-column type="checkbox" prop="schecked" label=""></el-table-column>
  </el-table>

  <router-view></router-view>
</div>
</template>
<script>
import ElCheckbox from 'components/checkbox';
import ElSelect from 'components/select';
import ElOption from 'components/option';
import ElInput from 'components/input';
import ElButton from 'components/button';
import ElTag from 'components/tag';
import ElTable from 'components/table';
import ElTableColumn from 'components/table-column';
import Tooltip from 'components/tooltip';
import CustomTable from 'components/custom-table'
import CustomRow from 'components/custom-row'
import CustomColumn from 'components/custom-column'
import AddressBox from 'components/address-box'

// 
const CONFIG = require('constant').CONFIG;

export default{
  name: 'home',
  components:{
   ElCheckbox, ElSelect, ElOption, ElInput, ElTable,
   ElTableColumn, ElButton, ElTag, Tooltip,
   CustomTable,CustomRow, CustomColumn, AddressBox
  },
  props: {

  },
  data () { return {
    load: true,
    bcheck: false,
    mselects: '1',
    options: [{
      value: '1',
      label: ''
    }, {
      value: '2',
      label: ''
    }, {
      value: '3',
      label: ''
    }, {
      value: '4',
      label: ''
    }, {
      value: '5',
      label: ''
    }],
    inputa: 'in A',
    inputb: 'in B',
    inputc: 'in C',
    address: '11-1101-110101',
    tableData: [{
      date: '2016-05-03',
      name: '',
      address: ' 1518 ',
      schecked: false,
      choose:'1'
    },{
      date: '2016-05-07',
      name: '',
      address: ' 1518 ',
      schecked: true,
      choose:'2'
    }],
    multipleSelection: [],
    fullscreenLoading: false
  }},
  methods:{
    handleClick() {
      console.log(1);
    },
    openFullScreen() {
      this.fullscreenLoading = true;
      setTimeout(() => {
        this.fullscreenLoading = false;
      }, 3000);
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    }
  }

}
</script>



